body {
  background: #fff;
  color: #383838;
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 15px;
  font-size: 15px;
  font-weight: 500;
  line-height: 1.667;
  letter-spacing: .015em;
}

.out {
  overflow: hidden;
}

.inner {
  max-width: 1144px;
  padding: 0 15px;
  margin: 0 auto;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.inner_sm {
  max-width: 1000px;
}

.particle {
  position: absolute;
  width: 9px;
  height: 9px;
  border-radius: 3px;
  background-color: #383838;
}

.particle_sm {
  width: 5px;
  height: 5px;
  border-radius: 2px;
}

.particle_lg {
  width: 14px;
  height: 14px;
}

.particle_xl {
  width: 17px;
  height: 17px;
  border-radius: 5px;
}

.particle_rotate-thirty {
  transform: rotate(30deg);
}

.particle_rotate-fourthyfive {
  transform: rotate(45deg);
}

.btn {
  padding: 18px 52px;
  border-radius: 28px;
  height: 55px;
  color: #fff;
  background-color: #9b7de8;
  text-transform: uppercase;
  outline: none;
  font-weight: 700;
  transition: transform .3s;
  font-family: "Montserrat", Helvetica, sans-serif;
  letter-spacing: 0.5px;
}

.no-touch .btn:hover {
  transform: scale(1.05);
}

.btn_white {
  background-color: #fff;
  color: #9b7de8;
}

.btn_wide {
  width: 100%;
}

.btn_bold {
  font-weight: 900;
}

.btn-down {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  color: #fff;
  background-color: #9b7de8;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  transition: transform .3s;
}

.no-touch .btn-down:hover {
  transform: scale(1.05);
}

.btn-down:after {
  content: "";
  position: absolute;
  left: -5px;
  top: -5px;
  bottom: -5px;
  right: -5px;
  border-radius: 50%;
  background-color: #9b7de8;
  opacity: 0.2;
}

