.about-eric {
	position: relative;
	font-family: $montserrat;
	padding-top: 145px;
	padding-bottom: 75px;

	&__inner {
		position: relative; }

	&__background {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;

		.icon {
			position: absolute;
			font-size: 563px;
			stroke: $violet;
			opacity: .1;
			left: -382px;
			top: 98px; } }

	&__particle-first {
		left: -145px;
		top: 252px; }

	&__particle-second {
		bottom: 238px;
		right: -26px; }

	&__particle-third {
		bottom: 121px;
		left: -144px; }

	&__particle-fourth {
		bottom: 82px;
		right: -152px; }

	&__particle-fifth {
		bottom: -30px;
		left: 112px; }


	&__wrapper {
		position: relative;
		display: flex;
		justify-content: space-between; }


	&__phone {
		width: 35%;
		margin-top: 16px;
		@include sm {
			display: none; } }

	&__phone-image {
		width: 100%; }

	&__text {
		width: 56%;
		@include sm {
			width: 100%; } }

	&__title {
		font-size: 36px;
		font-weight: 900; }

	&__descr {
		letter-spacing: 0.015em;
		margin-top: 32px;
		font-size: 15px;
		font-weight: 500;
		color: $black;
		line-height: 1.667; }

	&__break {
		line-height: 3.1; } }
