.hero {
	position: relative;
	height: 100vh;
	min-height: 630px;
	background-color: rgba($color: #2b2e26, $alpha: 0.5);
	@include sm {
		height: auto; }
	.error {
		color: #fff; }
	&__video {
		position: relative;
		height: 100%;
		width: 100%;
		@include sm {
			position: absolute;
			top: 0;
			left: 0; }
		&:before {
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			right: 0;
			content: "";
			z-index: 1;
			background-color: rgba(#142338, 0.45); } }
	&__container {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		z-index: 4;
		color: #fff;
		padding: 133px 0 136px;
		letter-spacing: 0;
		@include vertical;
		@include md {
			padding: 49px 0 0; }
		@include sm {
			position: relative; }
		&:before {
			@include sm {
				display: none; } } }
	&__content {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		font-size: 15px;
		text-align: left;
		letter-spacing: .5px;
		@include sm {
			display: block;
			padding-top: 30px; } }
	&__footer {
		margin-top: 40px;
		display: flex;
		align-items: center; }
	&__consultation {
		margin-left: 30px;
		color: $lightGray;
		font-weight: 700; }
	&__date {
		font-size: 18px;
		font-weight: 700;
		opacity: 0.9; }
	&__title {
		margin-top: 17px;
		font-size: 48px;
		font-weight: 900; }
	&__text {
		margin-top: 20px;
		max-width: 555px;
		letter-spacing: normal;
		line-height: 1.74; }
	&__scroll {
		position: absolute;
		bottom: 32px;
		left: 50%;
		transform: translateX(-50%); }
	&__dots {
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 16px; }
	&__dot {
		display: block;
		width: 4px;
		height: 4px;
		background-color: $white;
		border-radius: 50%;
		opacity: 0.5;
		&.is-active {
			width: 8px;
			height: 8px;
			opacity: 1; }
		&:not(:first-child) {
			margin-top: 11px; } } }
